<template>
  <main>
      <div id="register">
          <div class="input-field">
              <p class="id-tip">{{tip.id}}</p>
              <input @input="checkValue()" v-model="registerInfo.id" type="text" placeholder="ID ">
              <button @click="checkID()" :disabled="tip.id.length > 0 || registerInfo.id.length <= 0" class="id-check">ID  </button>
              <p class="password-tip">{{tip.password}}</p>
              <input @input="checkValue()" v-model="registerInfo.password" type="password" placeholder=" ">
              <p class="repassword-tip">{{tip.checkPassword}}</p>
              <input @input="checkValue()" v-model="registerInfo.checkPassword" type="password" placeholder="  ">
              <p class="name-tip">{{tip.name}}</p>
              <input @input="checkValue()" v-model="registerInfo.name" type="text" placeholder=" ">
              <p class="code-tip">{{tip.code}}</p>
              <input @input="checkValue()" v-model="registerInfo.code" type="text" placeholder="    ">
          </div>
          <div class="btn-field">
              <button @click="register()" :disabled="tip.id.length + tip.password.length + tip.checkPassword.length + tip.name.length + tip.code.length > 0 || registerInfo.id.length <= 0 || registerInfo.password.length <= 0 || registerInfo.checkPassword.length <= 0 || registerInfo.name.length <= 0 || registerInfo.code.length <= 0 || !registerInfo.idCheck"></button>
              <router-link to="/login"></router-link>
          </div>
      </div>
      <router-link to="/" class="main-page">  </router-link>
  </main>
</template>

<script>
import { ref, getCurrentInstance, onBeforeMount } from 'vue';

export default {
    setup() {
        const { proxy } = getCurrentInstance();

        const registerInfo = ref({
            id: '',
            password: '',
            checkPassword: '',
            name: '',
            code: '',
            idCheck: false,
        });

        const tip = ref({
            id: '',
            password: '',
            checkPassword: '',
            name: '',
            code: '',
        });

        const register = async () => {
            await proxy.axios.post('/register_process', {
                id: registerInfo.value.id,
                password: registerInfo.value.password,
                name: registerInfo.value.name,
                code: registerInfo.value.code,
            });

            alert('  !');
            location.href = '/login';
        };

        const checkValue = () => {
            const specialPattern = /[!@#$%^&*()_+={}:;<>?/|.,`~'"[\]\\-]/gi;
            const includePattern = /[!@#$%^&*?.]/gi;
            const decludePattern = /[()_+={}:;<>/|,`~'"[\]\\-]/gi;
            const engPattern = /[A-Za-z]/;
            const numberPattern = /[0-9]/;

            if(registerInfo.value.id.length !== 0) {
                if(registerInfo.value.id.length < 8 || registerInfo.value.id.length > 20) tip.value.id = 'ID 8 , 20  .';
                else if(registerInfo.value.id.search(/\s/) != -1) tip.value.id = '   .';
                else if(specialPattern.test(registerInfo.value.id)) tip.value.id = '    .';
                else if(numberPattern.test(registerInfo.value.id.split('')[0])) tip.value.id = '   .';
                else tip.value.id = '';

                registerInfo.value.id.split('').forEach(res => {
                    if(!engPattern.test(res) && !numberPattern.test(res)) tip.value.id = '    ';
                });
            }

            if(registerInfo.value.password.length !== 0) {
                if(registerInfo.value.password.length < 8 || registerInfo.value.password.length > 20) tip.value.password = ' 8 , 20  .';
                else if(registerInfo.value.password.search(/\s/) != -1) tip.value.password = '   .';
                else if(decludePattern.test(registerInfo.value.password)) tip.value.password = '    .';
                else if(!includePattern.test(registerInfo.value.password)) tip.value.password = '!,@,#,$,%,^,&,*,?,.   .';
                else if(numberPattern.test(registerInfo.value.password.split('')[0])) tip.value.password = '   .';
                else tip.value.password = '';
            }

            if(registerInfo.value.checkPassword.length !== 0) {
                if(registerInfo.value.checkPassword !== registerInfo.value.password) tip.value.checkPassword = '  .';
                else tip.value.checkPassword = '';
            }

            if(registerInfo.value.name.length !== 0) {
                if(registerInfo.value.name.length > 20) tip.value.name = ' 20  .';
                else if(registerInfo.value.name.search(/\s/) != -1) tip.value.name = '   .';
                else if(decludePattern.test(registerInfo.value.name)) tip.value.name = '    .';
                else tip.value.name = '';
            }

            if(registerInfo.value.code.length !== 0) {
                if(registerInfo.value.code.length !== 8) tip.value.code = '    8.';
                else if(registerInfo.value.code.search(/\s/) != -1) tip.value.code = '   .';
                else if(specialPattern.test(registerInfo.value.code)) tip.value.code = '    .';
                else tip.value.code = '';

                registerInfo.value.code.split('').forEach(res => {
                    if(!numberPattern.test(res)) tip.value.code = '       .';
                });
            }
        }

        const checkID = async () => {
            let { data } = await proxy.axios.post('/check_id', {
                id: registerInfo.value.id
            });

            registerInfo.value.idCheck = data;

            if(!registerInfo.value.idCheck) alert(' ID !');
            else alert(' ID  !');
        }

        onBeforeMount(() => {
            // if(window.sessionStorage.getItem('jwt-auth-token') !== null) location.href = '/';
        })

        return { registerInfo, tip, register, checkValue, checkID };
    }
}
</script>

<style scoped>
main {
    width: 100%;
    height: 100%;
}

.main-page {
  width: 4.5%;
  height: 100%;
  border: 2px solid #ffc038;
  border-left: none;
  border-radius: 0 15px 15px 0;
  color: #ffc038;
  text-shadow: -0.5px 0 #000000, 0 0.5px #000000, 0.5px 0 #000000, 0 -0.5px #000000;
  position: absolute;
  top: 0;
  left: 0;
  writing-mode: vertical-lr;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: all 1.5s;
  font-size: 3rem;
}
    
.main-page:hover {
  opacity: 1;
}
</style>