<template>
  <main>
      <div id="login">
            <p class="title">LOGIN PAGE</p>
            <div class="input-field">
                <input @keyup.enter="login()" v-model="loginInfo.id" type="text" class="id" placeholder="ID " onfocus="this.select()">
                <input @keyup.enter="login()" v-model="loginInfo.password" type="password" class="password" placeholder=" " onfocus="this.select()">
                <button @click="login()"></button>
            </div>
            <div class="advice">
                <router-link to="/register">  ?</router-link>
                <router-link to="" @click="preventLocation()">ID/ ?</router-link>
            </div>
      </div>
      <router-link to="/" class="main-page">  </router-link>
  </main>
</template>

<script>
import { ref, getCurrentInstance, onBeforeMount, onMounted } from 'vue';

export default {
    setup() {
        const { proxy } = getCurrentInstance();
        const loginInfo = ref({
            id: '',
            password: '',
        });

        const login = async () => {
            if(loginInfo.value.id.length > 0 && loginInfo.value.password.length > 0) {
                let { data, headers } = await proxy.axios.post('/login_process', {
                    id: loginInfo.value.id,
                    password: loginInfo.value.password,
                });

                if(data.status) {
                    window.sessionStorage.setItem('jwt-auth-token', headers['jwt-auth-token']);
                    window.sessionStorage.setItem('name', data.data.name);

                    alert(' !');
                    location.href = '/';
                } else {
                    alert(' . ID     .');
                }
            } else {
                alert('ID   ');
            }
        };

        const preventLocation = () => {
            alert('');
            location.href = '/login';
        };

        onBeforeMount(() => {
            // if(window.sessionStorage.getItem('jwt-auth-token') !== null) location.href = '/';
            // else window.localStorage.clear();
        });

        onMounted(() => {
            // document.querySelector('.id').focus();
        });

        return { loginInfo, login, preventLocation };
    }
}
</script>

<style scoped src="../../../css/community/user/loginComponent.css" />